@use '../abstracts' as *;

header.main-hero {
  position: relative;
  min-height: 300px;
  height: 40vh;
  background-color: $color-primary;
  background-size: cover;
  background-position: center;
  overflow: hidden;
  transition: height 0.3s ease;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .main-hero-text {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width: 100%;

    .main-hero-title,
    .main-hero-subtitle {
      color: $bright;
      margin: 0.5rem 0;
    }
  }
  &.is-home {
    height: 100vh;
    background-position: 80%;
    background-attachment: fixed;
  }
}
