@use '../abstracts' as *;

.main-navigation {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  z-index: 1;
  transition: background $def-trans-dur $def-trans-timing,
    transform $def-trans-dur $def-trans-timing;

  .nav-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 1em auto;
    color: $bright;

    .nav-logo {
      margin: 0;
    }
    .nav-icon {
      a {
        opacity: 0.3;
        transition: opacity $def-trans-dur ease;

        &:hover {
          opacity: 1;
        }

        svg {
          font-size: 1.4rem;
        }
      }
    }
  }
  &.is-scroll {
    background-color: rgba($dark-gray, 0.9);
    z-index: 10;
  }
  &.scrolling {
    transform: translateY(-100%);
    transition-delay: 0.3s;
  }
}

.close-trigger,
.menu-trigger {
  display: block;
  width: 42px;
  height: 42px;
  cursor: pointer;
}

.mobile-menu {
  @include show-in-mobile();
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  visibility: hidden;
  z-index: 1;
  transition: all 0.1s ease 0.5s;

  &::before,
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform: translateX(-100%);
    transition: transform 450ms $def-trans-timing;

    @include media('screen', '<tablet') {
      will-change: transform;
    }
  }
  &::before {
    transition-delay: 0.25s;
    background-color: $gray;
    background: linear-gradient(270deg, $gray, lighten($gray, 10%));
    z-index: 24;
  }
  &::after {
    transition-delay: 0.15s;
    background-color: $dark-gray;
    background: linear-gradient(270deg, $dark-gray, lighten($dark-gray, 10%));
    z-index: 25;
  }
  .mobile-nav-links {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 15%;
    width: 70%;
    margin: 0;
    padding: 0;
    list-style: none;
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.4s $def-trans-timing;
    transition-delay: 0s;
    z-index: 26;

    a {
      display: inline-block;
      color: $bright;
      font-size: 1.6875rem;
      padding: 0.5em 1em;
      font-family: Arial, sans-serif;
    }
  }
  .close-trigger {
    position: absolute;
    top: 30px;
    right: 20px;
    transform: scale(0.75);
    opacity: 0;
    transition: opacity 0.25s ease 0s,
      transform 0.35s cubic-bezier(0.694, 0.048, 0.335, 1) 0s;
    z-index: 46;

    .close-trigger-bar {
      display: block;
      width: 100%;
      height: 4px;
      background-color: #e3d8d3;
      position: relative;

      &.left {
        transform: rotate(-45deg);
      }
      &.right {
        transform: rotate(45deg);
        top: -3px;
      }
    }
  }
}

.is-mobile-menu-open .mobile-menu {
  visibility: visible;
  transition-delay: 0.1s;
  z-index: 45;

  &::before,
  &::after {
    transform: translateX(0);
  }
  &::before {
    transition-delay: 0s;
  }

  .mobile-nav-links {
    opacity: 1;
    transition-delay: 0.4s;
    transform: translateY(-60%);
  }
  .close-trigger {
    transform: scale(1);
    opacity: 1;
    transition-delay: 0.52s;
  }
}
