@use './variables' as *;
@use './functions' as func;

// Media query mixin
@mixin media($conditions...) {
  @for $i from 1 through length($conditions) {
    $conditions: set-nth(
      $conditions,
      $i,
      func.parse-expression(nth($conditions, $i))
    );
  }

  $branches: func.get-query-branches($conditions);
  $query: '';

  @each $branch in $branches {
    @if (str-length($query) != 0) {
      $query: $query + ', ';
    }

    $query: $query + $branch;
  }

  @media #{$query} {
    @content;
  }
}

// Deafult container mixin
@mixin container($maxWidth: $max-width) {
  width: 100%;
  max-width: calc(#{$maxWidth} + #{$desktop-padding});
  padding: 0 $desktop-padding;
  margin: 0 auto;
  transition: padding $def-trans-dur $def-trans-timing;

  @include media('screen', '>=tablet', '<=desktop') {
    max-width: calc(#{$maxWidth} + #{$tablet-padding});
    padding: 0 $tablet-padding;
  }

  @include media('screen', '<tablet') {
    max-width: calc(#{$maxWidth} + #{$mobile-padding});
    padding: 0 $mobile-padding;
  }
}

// Show/Hide when Mobile with transition
@mixin show-in-mobile($mode: '') {
  @if $mode == fade {
    opacity: 0;
    visibility: hidden;
    transition: visibility 0s linear $def-trans-dur,
      opacity $def-trans-dur $def-trans-timing;

    @include media('screen', '<tablet') {
      opacity: 1;
      visibility: visible;
      transition: visibility 0s, opacity $def-trans-dur $def-trans-timing;
    }
  } @else {
    display: none;
    @include media('screen', '<tablet') {
      display: block;
    }
  }
}

@mixin hide-in-mobile($mode: '') {
  @if $mode == fade {
    @include media('screen', '<tablet') {
      opacity: 0;
      visibility: hidden;
      transition: visibility 0s linear $def-trans-dur,
        opacity $def-trans-dur $def-trans-timing;
    }
  } @else {
    @include media('screen', '<tablet') {
      display: none;
    }
  }
}
