/*********
 Fonts
 ********/
$font-size-base: 20;
$font-size-base-px: $font-size-base * 1px;
$font-size-base-narrow: 16;
$font-size-base-narrow-px: $font-size-base-narrow * 1px;

$leading-base: 1.4;
$line-height-base: round($font-size-base * $leading-base); // 20 * 1.4
$baseline: $line-height-base * 1px;

/*********
 Colors
 ********/
$blue: #288ad6;
$green: #16c98d;
$red: #fa5e5b;
$yellow: #feef6d;
$orange: #fe7c08;
$pink: #ff708e;
$purple: #8e44ad;
$gray: #95a5a6;
$dark-gray: #1c1d25;

$dark: #000;
$bright: #fff;

// Color Scheme
$color-primary: $dark-gray;
$color-secondary: $orange;
$color-highlight: $blue;

// Common colors
$color-text: #444;
$color-bg: #f1f1f1;
$color-border: #ddd;

// Links
$color-link-default: $color-highlight;
$color-link-visited: $color-link-default;
$color-link-hover: darken($color-link-default, 20%);
$color-link-active: darken($color-link-default, 20%);

// Text Selection
$color-selection: #fff;
$color-selection-bg: $color-primary;

// <hr> border color
$hr-color: lighten(#000, 70%);

// scoial media color
$color-twitter: #55acee;
$color-facebook: #3b5998;
$color-youtube: #cd201f;
$color-pinterest: #bd081c;
$color-instagram: #e4405f;
$color-whatsapp: #25d366;

/*********
 Times
 ********/
$def-trans-dur: 300ms;
$def-trans-timing: cubic-bezier(0.694, 0.048, 0.335, 1);

/*********
 Layout
 ********/
$desktop-padding: 40px;
$tablet-padding: 30px;
$mobile-padding: 20px;

$max-width: 1170px;

/*********
 Breakpoints
 ********/
$breakpoints: (
  'phone': 576px,
  'tablet': 768px,
  'desktop': 1024px,
) !default;
$media-expressions: (
  'screen': 'screen',
  'print': 'print',
  'handheld': 'handheld',
  'retina2x': (
    '(-webkit-min-device-pixel-ratio: 2)',
    '(min-resolution: 192dpi)',
  ),
  'retina3x': (
    '(-webkit-min-device-pixel-ratio: 3)',
    '(min-resolution: 350dpi)',
  ),
) !default;
$unit-intervals: (
  'px': 1,
  'em': 0.01,
  'rem': 0.1,
) !default;
