@use '../abstracts' as *;

main.content {
  color: $bright;

  section {
    position: relative;
    overflow: hidden;
    padding: 2.5rem 0;

    .container {
      position: relative;

      h2 {
        margin-top: 0;
      }
    }
  }

  .div-center {
    max-width: 530px;
    margin: 0 auto;
  }
}
#project-section {
  background-color: #202020;
}
#about-me-section {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: block;
    background-color: rgba(0, 0, 0, 0.75);
  }
}
#contact-me {
  background-color: #202020;
}
