@use '../abstracts' as *;

.loader-container {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #323442;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  z-index: 9999;

  .loader-text {
    color: #bbcedd;
  }
  &.active {
    opacity: 1;
    visibility: visible;
  }
  &:not(.active) {
    transition: opacity $def-trans-dur $def-trans-timing,
      visibility $def-trans-dur $def-trans-timing;
  }
}
