@use '../abstracts' as *;

.project-items {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .project-item {
    display: flex;
    flex-direction: column;
    flex: 0 1 33.3333%;
    padding: 0 15px;
    margin-bottom: 1.5rem;

    .project-image {
      margin-bottom: 1rem;
    }
    .project-content {
      text-align: center;
      height: 100%;

      &__name {
        font-size: 1.2rem;
        margin-bottom: 0.5rem;
      }
      &__link {
        margin-bottom: 0.5rem;

        .no-result {
          font-size: 0.8rem;
          padding: 11px 0;
        }
      }
      &__languages {
        display: flex;
        align-items: center;
        font-size: 0.8rem;

        .icon {
          align-self: flex-start;
          font-size: 1rem;
          margin-right: 0.5rem;
        }
      }
    }
  }

  @include media('screen', '<desktop') {
    .project-item {
      flex: 0 1 50%;
    }
  }
  @include media('screen', '<phone') {
    .project-item {
      flex: 0 1 100%;

      .project-content__languages {
        justify-content: center;
      }
    }
  }
}
.project-link {
  opacity: 0.4;
  transition: opacity 0.3s ease;

  &:hover {
    opacity: 1;
  }
  .icon {
    font-size: 1.5rem;
  }
}
