@use '../abstracts' as *;

html,
body {
  font-size: $font-size-base-px;
  line-height: $leading-base;
  color: $color-text;
  overflow-x: hidden;
  background-color: #333;

  @include media('screen', '<tablet') {
    font-size: $font-size-base-narrow-px;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  // color: $color-primary;
  @include media('screen', '<tablet') {
    line-height: 1.4;
  }
}

h1 {
  font-size: 3.5rem;

  @include media('screen', '<tablet') {
    font-size: 2rem;
  }
}
h2 {
  font-size: 2.8rem;
  font-weight: 400;

  @include media('screen', '<tablet') {
    font-size: 1.8rem;
  }
}

p {
  line-height: 1.8;

  &:last-child {
    margin-bottom: 0;
  }
}
