@use '../abstracts' as *;

footer#copyright {
  color: $bright;
  font-size: 0.8rem;
  background-color: #333;
  padding: 1.2rem 0;

  a {
    opacity: 0.3;
    transition: opacity $def-trans-dur ease;

    &:hover {
      opacity: 1;
    }
    svg {
      font-size: 1.6rem;
    }
  }
}
